import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  InputGroup,
  InputGroupAddon,
  Label,
  Spinner,
} from 'reactstrap';
import { Separator } from '../../../../components/common/CustomBootstrap';
import IntlMessages from '../../../../helpers/IntlMessages';
import { getProfileDataAsync } from '../../../../helpers/Utils';
import instance from '../../../../helpers/axiosInstance';

const UploadResume = ({ editResume, toggleEditResume, profileData }) => {
  const [loading, setLoading] = useState(false);
  console.log(profileData);
  
  const [values, setValues] = useState({});
  const formData = new FormData();

  const handleFileChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.files[0] });
  };

  const handleUpload = async (event) => {
    setLoading(true);

    formData.append('id', profileData.id);
    formData.append('user_id', profileData.user_id);
    formData.delete(event.target.id);

    formData.append(`${event.target.id}`, values[event.target.id]);

    await instance({
      url: '/updateProfile',
      method: 'POST',
      headers: {
        'Content-Type': 'multipart/form-data',
      },
      data: formData,
      timeout: 100000,
    });

    await getProfileDataAsync(profileData.user_id);
    
      setLoading(false);
      toggleEditResume(!editResume);
  };

  return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          onClick={() => {
            toggleEditResume(!editResume);
          }}
          className="icon-button"
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>
      <CardBody>
        <CardTitle>
          <h2 className="lead">
            <IntlMessages id="user.upload-resume" />
          </h2>
        </CardTitle>

        <Separator className="mb-3" />

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.upload-resume" />
          </Label>
          {loading ? (
            <div className="mb-3 text-center">
              <Spinner color="primary" className="mb-1" />
            </div>
          ) : (
            <InputGroup>
              <CustomInput
                type="file"
                id="resume"
                name="resume"
                onChange={handleFileChange}
                disabled={!editResume}
              />
              <InputGroupAddon addonType="append">
                <Button
                  color="secondary"
                  onClick={handleUpload}
                  disabled={!editResume}
                >
                  <IntlMessages id="forms.upload-button" />
                </Button>
              </InputGroupAddon>
            </InputGroup>
          )}
        </FormGroup>
      </CardBody>
    </Card>
  );
};

export default UploadResume;
